.App { width:100%;height:100%;position:relative;overflow-x: hidden;overflow-y: scroll;}

.BodyContent{top:100px;position: relative;}
.content1{ position:relative;height: 680px;}
.androidheader{position: relative;left: 39%;width: 18%;}
.phone{position:absolute;left: 70%;width: 80%;max-width: 81em;top:0%}
.androidphoneimg{ width: 274.1px;z-index: 2;position: absolute;}
