.content2{height:600px;position:relative;height: fit-content;}
#farmerAppliation{height:35em;width:100%;position:absolute;right:30%;top:-9%}
#container1{left: 41.5%;position: relative;height: fit-content;width: fit-content;}
#Tractor{ height: 35.1em; width: fit-content; position: absolute; bottom:-16em;}
#Tractor canvas {width: 100% !important;height: 100% !important; }
#farmLand {top: 20%;z-index: -1;height: 390px;width: 875px;position: relative;margin: 0 auto;box-shadow: 3px 3px 9px rgb(0 0 0 / 60%);}
#farmLand img {
  position:absolute;left:0;height:100%;width:100%;
  -webkit-transition: opacity 1s ease-in-out;
  -moz-transition: opacity 1s ease-in-out;
  -o-transition: opacity 1s ease-in-out;
  transition: opacity 1s ease-in-out;
}
#farmLand img.top {
animation-name: cf3FadeInOut;
animation-timing-function: ease-in-out;
animation-iteration-count: infinite;
animation-duration: 68s;
animation-direction: normal;
}
@keyframes cf3FadeInOut {
0% {
  opacity:1;
}
99% {
opacity:0;
}
100% {
opacity:1;
}
}